.mobilityWrapper {
  margin-top: 16px;
  & > div {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.btn {
  background: var(--nightblue-20);
  color: var(--nightblue-100);
}
.title {
  width: 100%;
  text-align: center;
  font-weight: 500;
  font-size: 34px;
  color: #fff;
  margin-top: 16px;
  margin-bottom: 50px;
}
.searchBtn {
  padding: 14px 0;
}

.altere {
  font-weight: 500;
  font-size: 27px;
  line-height: 31px;
  color: var(--nightblue-90);
  margin: 0px 24px;
}

.informationTrainer {
  .amountView {
    display: flex;
    align-items: center;
    margin-left: 8px;
    padding: 4px 10px;
    font-weight: 500;
    background: var(--violet-10);
    border-radius: 8px;
    color: var(--nightblue-100);
    @media screen and (max-width: 360px) {
      margin-left: 0;
    }
    i {
      font-size: 20px;
      margin-right: 8px;
      color: var(--violet-60);
    }
    span {
      font-size: 13px;
    }
  }
}

.hashtag {
  padding: 7px 10px 8px;
  width: 109px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: var(--violet-20);
  border-radius: 16px;
}

.content {
  margin-top: 10px;
  color: var(--nightblue-100);
  font-weight: 500;
  font-size: 16px;
  cursor: pointer;
  word-break: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.hbSave {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 2px solid var(--violet-100);
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--violet-100);
  cursor: pointer;
  .iconCross {
    font-size: 15px;
    font-weight: 900;
  }
}

.noVideo {
  font-weight: 500;
  font-size: 16px;
  color: var(--nightblue-100);
  margin-left: 20px;
}

.thumbnail {
  @media screen and (max-width: 860px) {
    margin-right: 15px !important;
    width: 94px !important;
    height: 94px !important;
  }
}

.itemContent {
  @media screen and (max-width: 860px) {
    height: auto !important;
  }
}

.tags {
  margin-top: 10px;
}

.wrapperContainer {
  @media screen and (max-width: 480px) {
    margin: 0 0 30px;
  }
}

.itemInfo {
  @media screen and (max-width: 860px) {
    margin-right: 10px !important;
    max-width: calc(100% - 124px);
  }
  @media screen and (max-width: 480px) {
    margin-right: 0 !important;
    max-width: calc(100% - 114px);
  }
}

.hashTag {
  & > span {
    padding: 0 10px;
    margin: 0 !important;
  }
}

.avatar {
  @media screen and (max-width: 480px) {
    width: 24px !important;
    height: 24px !important;
  }
  @media screen and (max-width: 360px) {
    display: none;
  }
}
